
import { defineComponent } from 'vue'

const Author = require('../../assets/avatar.jpg') // eslint-disable-line @typescript-eslint/no-var-requires
const Avatar = require('../../assets/author.jpg') // eslint-disable-line @typescript-eslint/no-var-requires

export default defineComponent({
  name: 'viewAvatar',
  setup () {
    const author = Author
    const avatar = Avatar
    const fit = 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'

    const base = `<ul class="list float_l">
  <li>
    <fx-avatar :src="author" size="large" class="mr20"/>
  </li>
  <li>
    <fx-avatar :src="author" size="middle" class="mr20"/>
  </li>
  <li>
    <fx-avatar :src="author" size="default" class="mr20"/>
  </li>
  <li>
    <fx-avatar :src="author" size="mini" class="mr20"/>
  </li>
</ul>
<ul class="list float_l">
  <li>
    <fx-avatar :src="avatar" size="large" class="mr20" shape="square"/>
  </li>
  <li>
    <fx-avatar :src="avatar" size="middle" class="mr20" shape="square"/>
  </li>
  <li>
    <fx-avatar :src="avatar" size="default" class="mr20" shape="square"/>
  </li>
  <li>
    <fx-avatar :src="avatar" size="mini" class="mr20" shape="square"/>
  </li>
</ul>`
    const type = `<fx-avatar :src="author" class="mr20"></fx-avatar>
<fx-avatar icon="icon-user" class="mr20"></fx-avatar>
<fx-avatar class="mr20">user</fx-avatar>`
    const error = `<fx-avatar src="http://error" size="large" class="mr20">
  <i class="iconfont icon-user fs30"/>
</fx-avatar>`
    const objectFit = `<fx-avatar :src="fit" fit="fill" :size="100" shape="square" class="mr20"></fx-avatar>
<fx-avatar :src="fit" fit="contain" :size="100" shape="square" class="mr20"></fx-avatar>
<fx-avatar :src="fit" fit="cover" :size="100" shape="square" class="mr20"></fx-avatar>
<fx-avatar :src="fit" fit="none" :size="100" shape="square" class="mr20"></fx-avatar>
<fx-avatar :src="fit" fit="scale-down" :size="100" shape="square" class="mr20"></fx-avatar>`

    return {
      author,
      avatar,
      fit,
      base,
      type,
      objectFit,
      error
    }
  }
})
